body, html {
  padding: 0;
  margin: 0;
  background-color: rgb(224, 224, 235);
  
}

.flex{
  display: flex;
}

.clm {
  flex-direction: column;
}

.header {
  background-color: rgb(49, 59, 146);
  text-align: center;
  color: white;
  padding: 10px;
  margin-bottom: 30px;
}

.menu{
  justify-content: flex-end;
}

.main{
   align-items: center;
}

.search{
  margin-bottom: 30px;
}

.btn{
  background-color: rgb(49, 59, 146) ;
  color: white;
  margin-left: 5px;
}

.navItem {
margin-right: 30px;
list-style: none;
}

.navItem:hover {
  cursor: pointer;
}


.table {
  background-color:  rgb(238, 236, 238);
  border: 1px solid rgb(49, 59, 146) ;
  }

.th {
  justify-content: space-between;
  align-items: center;
}

.th:hover{
  background-color: rgb(49, 59, 146);
  color: white;
  cursor: pointer;
}

.trUser:hover {
  background-color: lightgreen;
  color: white;
}

.about {
  justify-content: center;
  align-items: center;
}